export default {
	data() {
		return {
			height: '',
			items: ["今日订单", "历史订单"],
			current: 0,
			merchantId: '',
			orders: '',
			opens: -1,
			tg: true,
			height_a: "1px",
			orderDays: "",
		}
	},
	onLoad() {
		let that = this;
		uni.getStorage({
			key: 'userinfo',
			success(e) {
				that.merchantId = e.data.id;
				that.historical_order();
				that.orderDay();
			}
		});
	},
	onReady() {
		this.getTopheight();
	},
	methods: {
		getTopheight: function() {
			let that = this;
			uni.getSystemInfo({
				success(res) {
					that.height = res.statusBarHeight + 44 + "px";
					that.height_a = res.statusBarHeight + 44 + 50 + "px";
				}
			})
		},
		onTap: function(index) {
			if (this.current != index) {
				this.current = index;
			};
		},
		historical_order: function() {
			let that = this;
			uni.request({
				url: this.serveipd + '/api/merchant/order/orderStatistics',
				method: 'GET',
				data: {
					merchantId: that.merchantId,
				},
				success: res => {
					this.orders = res.data.data.datas;
				},
				fail: () => {},
				complete: () => {}
			});
		},
		orderDay: function() {
			let that = this;
			uni.request({
				url: this.serveipd + '/api/merchant/order/orderDay',
				method: 'GET',
				data: {
					merchantId: that.merchantId,
				},
				success: res => {
					console.log(res);
					that.orderDays = res.data.data.datas;
				},
				fail: () => {},
				complete: () => {}
			});
		},
		open: function(index) {
			this.tg = !this.tg;
			if (this.opens != index) {
				this.opens = index;
			} else {
				this.opens = -1;
			}
		},
		wz: function(latitude, longitude) {
			uni.openLocation({
				latitude: latitude,
				longitude: longitude,
				success: function() {
					console.log('success');
				}
			});
		},
		dh: function(phone) {
			uni.makePhoneCall({
				phoneNumber: phone,
				success: function(res) {
					console.log(res)
				}
			});
		},
	}
}
